<template>
  <div v-if="isshow">
    <header>
      <h3>后台信息管理</h3>
      <div>
        <span>欢迎登陆：</span>
        <span v-text="isuser"></span>

        <router-link to>
          <span style="color:red;" @click="adminout"> 退出登录</span>
        </router-link>
      </div>
    </header>
    <el-tabs
      :tab-position="tabPosition"
      style="height:100vh;width:100vw;position: fixed;left:0;top:50px;background: #F9F9F9;"
    >
      <el-tab-pane label="后台首页">
        <div>
          <h1>申请信息</h1>
          <el-table
            :data="tableData"
            border:true
            style="width: 100%;height:703px"
            @selection-change="handleSelectionChange"
          >
            <el-table-column fixed prop="riqi" label="日期" sortable column-key="riqi" width="150"></el-table-column>
            <el-table-column prop="uname" label="姓名" width="120"></el-table-column>
            <el-table-column prop="phone" label="手机号" width="120"></el-table-column>
            <el-table-column prop="idnumber" label="身份证" width="200"></el-table-column>
            <el-table-column prop="addressite" label="单位地址" width="330"></el-table-column>
            <el-table-column prop="cname" label="单位名称" width="150"></el-table-column>
            <el-table-column fixed="right" label="操作" width="100">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            background
            layout="prev, pager, next"
            :total="sum"
            @current-change="handleCurrentChange"
          ></el-pagination>
        </div>
      </el-tab-pane>
      <!-- <el-tab-pane label="申请信息">配置管理</el-tab-pane>
      <el-tab-pane label="页面更改">角色管理</el-tab-pane> -->
    </el-tabs>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tabPosition: "left",
      tableData: [],
      multipleSelection: [],
      page: 1,
      size: 11,
      sum: 0,
      isuser: "",
      isshow:false
    };
  },
  methods: {
    handleClick(row) {
      var qid = row.qid;
      if (qid != "") {
        this.axios.post("index/api/user/del", { qid: qid }).then(res => {
          if (res.data.code == 1) {
            this.$message({
              message: "成功删除",
              type: "success"
            });
            this.user();
            this.count();
          } else {
            this.$message({
              message: "警告，操作过于频繁",
              type: "warning"
            });
          }
        });
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log(this.multipleSelection);
    },
    handleCurrentChange(val) {
      this.page = val;
      this.user();
    },
    user() {
      var page = this.page;
      var size = this.size;
      var obj = { page, size };
      this.axios.post("index/api/user", obj).then(res => {
        this.tableData = res.data;
      });
    },
    count() {
      this.axios.post("index/api/user/count").then(res => {
        this.sum = res.data[0].count;
      });
    },
    islogin() {
      this.axios.post("index/api/admin/islogin").then(res => {
        if (res.data.code == 1) {
          this.isuser = res.data.user;
          this.isshow=true
        } else {
          this.$router.push("/admin/login");
        }
      });
    },
    adminout() {
      this.axios.get("index/api/admin/out").then(res => {
        if (res.data.code == 1) {
          this.$message({
            message: "已退出登录",
            type: "success"
          });
          this.$router.push("/admin/login");
        }else{
          this.$message.error('出错了');
        }
      });
    }
  },
  beforeCreate() {
    
  },
  created() {
    this.islogin();
    this.user();
    this.count();
  }
};
</script>
<style scoped>
header {
  display: flex;
  width: 100%;
  height: 50px;
  background: #3e4047;
  color: #999;
  line-height: 50px;
  padding-left: 20px;
  justify-content: space-between;
  box-sizing: border-box;
}
header > div {
  margin-right: 20px;
}
.el-table--scrollable-x .el-table__body-wrapper {
  overflow: auto;
}
h1{
  margin:20px;
}
</style>